<template>
  <ul>
    <li v-for="mess in messages" :key="mess.id">
      <!-- to的字符串写法 -->
      <!-- <router-link :to="`/home/message/detail/${mess.id}/${mess.info}`">{{ mess.info }}</router-link> -->
      <router-link
        :to="{
          name: 'xiangqing',
          query: {
            id: mess.id,
            info: mess.info,
          },
        }"
        >{{ mess.info }}</router-link
      >
      <button @click="pushShow(mess)">push</button>
      <button @click="replaceShow(mess)">replace</button>
    </li>
    <hr />
    <router-view></router-view>
  </ul>
</template>

<script>
export default {
  name: "Message",
  data() {
    return {
      messages: [
        {
          id: "001",
          info: "白龍",
        },
        {
          id: "002",
          info: "我妻",
        },
        {
          id: "003",
          info: "長門",
        },
      ],
    };
  },
  methods: {
    pushShow(mess) {
      this.$router.push({
        name: "xiangqing",
        query: {
          id: mess.id,
          info: mess.info,
        },
      });
    },
    replaceShow(mess) {
      this.$router.replace({
        name: "xiangqing",
        query: {
          id: mess.id,
          info: mess.info,
        },
      });
    },
  },
};
</script>

<style>
button {
  margin-left: 10px;
}
li {
  margin-top: 5px;
}
</style>